.box {
  position: relative;
  height: 100%;
}
.input {
  padding-left: 35px;
}
.inputWrapper {
  position: relative;
}

.inputMove {
  padding-left: 43px;
}
.buttonBox {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  align-items: center;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
  gap: 5px;
  margin-right: 5px;
}
.buttonBox_mini {
  height: fit-content;
  position: relative;
  margin: 0;
  margin-top: 5px;
}
.button {
  color: white;
  font-size: 0.75rem;
}
.dollarLabel {
  position: absolute;
  left: 34px;
  top: 0;
  height: 100%;
  font-size: 16px;
  color: #000000;
  display: flex;
  align-items: center;
}
