@import "sass/index";
@import "../../../../variables";

.root {
  display: flex;
  flex-direction: column;
  position: relative;
}

.cell {
  width: $crashTrendsCellSize;
  height: $crashTrendsCellSize;
  border-radius: 2px;
  margin: 1px;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 600px) {
    width: $crashTrendsMCellSize;
    height: $crashTrendsMCellSize;
  }
}

.muchNumber {
  position: absolute;
  z-index: 3;
  margin: 0;
  left: $crashTrendsCellSize;
}
.dot {
  width: calc(#{$crashTrendsCellSize} * 0.6);
  height: calc(#{$crashTrendsCellSize} * 0.6);
  border-radius: 50%;
  background-image: linear-gradient(135deg, #00000040, transparent 50%);
  @media (max-width: 530px) {
    width: calc(#{$crashTrendsMCellSize} * 0.6);
    height: calc(#{$crashTrendsMCellSize} * 0.6);
  }
}
.red {
  background-color: $lose-red;
}
.green {
  background-color: $win-green;
}
