@import "sass/index";

.box {
  margin: 0;
  padding: 20px;
  overflow: hidden;
  position: relative;
}
.shadow {
  position: absolute;
  box-shadow: var(--template-gameCell-shadow);
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
}
.table {
  width: 100%;
}
.tableColumn {
  width: 100%;
  height: max-content;
  display: grid;
  grid-template-columns: 1fr 0.5fr 1fr 1fr;

  margin: 0 0 0.8rem;
}
.tableRow {
  padding: 0 5px;

  overflow-x: hidden;
  text-overflow: ellipsis;
  scrollbar-width: none;

  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
  font-family: "Gilroy", sans-serif;
  font-size: 0.75rem;
  letter-spacing: 2px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto;
}
.userAmountWrapper {
  position: relative;
  top: -0.34rem;

  overflow: visible;
}
.userAmount {
  position: absolute;
  top: 0.75rem;
}
.text {
  text-align: center;
  font-size: 0.75rem;
  margin: auto;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}
.textCoin {
  padding-left: 5px;
  margin: 0;
}
.textFail {
  color: use(--subsidiary-red) !important;
}
.textProfit {
  color: use(--subsidiary-blue) !important;
}
.btcIcon {
  position: static;
  height: 14px;
  width: 14px;
  cursor: default;
  color: black;
}
.userName {
  font-weight: bold;
}
.tableWrapper {
  overflow: hidden;
  height: 100%;
  overflow-y: auto;
  max-height: 500px;
}
