@import "sass/index";

.root {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
  gap: 5px;
  grid-gap: 5px;

  @media (max-width: $tabletScreen) {
    grid-template-columns: repeat(2, 1fr);
  }
}
.button {
  font-size: 1.125rem;
  font-weight: bold;
  font-family: "Gilroy", sans-serif;
  border-radius: 8px;
  max-width: 250px;
  width: 100%;
  background: use(--template-gameCell-gameButtons-crash) !important;
  color: use(--primary-main);
  transition: transform 0.5s;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 6px 16px;
  text-transform: uppercase;

  &:hover,
  &:active,
  &:focus {
    transform: scale(1.05);
  }
  &:disabled {
    opacity: 0.5;
  }

  @media (max-width: $smallScreen) {
    padding: 0;
  }
}
.buttonWrapper {
  height: 45px;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.buttonText {
  margin: 0;
  line-height: 100%;
  @media (max-width: $smallScreen) {
    font-size: 0.935rem;
  }
}
.helpButtonText {
  font-size: 0.7rem;
  display: block;
  margin: 0;

  @media (max-width: $smallScreen) {
    font-size: 0.5rem;
  }
}
