@import "sass/index";

$defaultMargin: 20px;

.root {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
  margin: 0;

  @media (max-width: $middleMediumScreen) {
    grid-template-columns: 1fr;
  }
  @media (min-width: $middleMediumScreen) and (max-width: $largeScreen) {
    grid-template-columns: 1fr;
  }
}
.betBox {
  margin-top: $defaultMargin;

  display: flex;
  flex-direction: column;
}

.text {
  text-transform: uppercase;
  font-weight: bold;
  margin: 0;
  color: use(--secondary--main);

  @media (max-width: $smallScreen) {
    font-size: 0.685rem;
  }
  opacity: 0.7;
}
.box {
  margin-top: $defaultMargin;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-column-gap: 10px;

  @media (max-width: $smallScreen) {
    grid-template-columns: 2fr 1fr;
  }
}
