@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.gameOverlay {
  background: transparent;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 4;
}
.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}
.gameBlock {
  overflow: hidden;
  border-radius: 30px;
  width: 100%;
  background: use(--template-gameBlock-background);

  display: grid;
  grid-template-columns: 7fr 3fr;
  grid-template-rows: minmax(1fr);

  @media (max-width: #{$mediumScreen}) {
    grid-template-columns: 1fr;
  }
}
.statisticsWrapper {
  padding-top: 26px;
}
.gameWrapper {
  position: relative;
}
.trendsMargin {
  margin-top: 230px;
  @media (max-width: #{$mobileScreen}) {
    margin-top: 200px;
  }
}
.gameCell {
  background: use(--template-gameCell-background);
  display: flex;
  flex-direction: column;
  justify-content: start;

  position: relative;
  overflow: hidden;
}
.gameFooter {
  position: relative;
  padding: 10px;

  & input {
    font-weight: 500;
  }
}
.historyWrapper {
  overflow-x: hidden;
}
.iconButtonsWrapper {
  position: absolute;
  top: 60px;
  right: 0;
  z-index: 4;

  @media (max-width: #{$smallScreen}) {
    top: 50px;
  }
}
.buttonIcon {
  color: --template-modal-closeIcon-color;
  opacity: 0.7;
}

.switchWrapper {
  position: absolute;
  top: 60px;
  left: 20px;
  & > .div {
    justify-content: flex-end;
  }
  & .p {
    opacity: 1;
    font-size: 0.75rem;
  }
}
