.input {
  padding: 12px;
  height: 40px;
  width: 100%;
  margin: 0;
  border-radius: 4px;
  border: none;
  font-size: 1rem;
  font-family: "Gilroy", sans-serif;
  outline: none;
  background: white;
  transition: box-shadow 0.4s;

  &:focus {
    box-shadow: 0px 0px 5px 1px rgb(130, 130, 130);
  }
}
