@import "sass/index";

$width: 400px, 550px, 600px, 700px, 800px, 960px, 1000px, 1100px, 1200px, 1281px,
  1400px, 1550px, 1670px, 1850px;
$margins: 5px, 10px, 10px, 20px, 25px, 1px, 5px, 10px, 15px, 5px, 10px, 15px,
  20px, 25px;
$steps: 14;

.elementBox {
  position: relative;
  display: flex;
  margin: 10px 5px;
  min-width: 50px;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
  cursor: pointer;

  @for $i from 1 to $steps {
    @media (min-width: nth($width, $i)) {
      margin: 10px nth($margins, $i);
    }
  }
}
.color {
  background: white;
  height: 13px;
  width: 13px;
  border-radius: 50%;
  margin-right: 5px;
}
.colorRed {
  background: use(--subsidiary-red);
}
.colorGreen {
  background: use(--subsidiary-blue);
}
.payout {
  margin: 0;
}
.payoutGreen {
  color: use(--subsidiary-blue);
}
.payoutRed {
  color: use(--subsidiary-red);
}
