.root {
  position: relative;
  height: min-content;
}
.box {
  position: relative;
  min-height: 75px;
  padding: 20px 3px;
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
}

.trendsWrapper {
  transition: transform 0.1s;
  transform: scaleY(1);
  transform-origin: 50% 0;
}

.disable {
  pointer-events: none;
  transform: scaleY(0);
  width: 100%;
  position: absolute;
}

.wrapper {
  position: relative;
  z-index: 4;
}

.animationWrapper {
  width: 100%;
  display: flex;
  justify-content: center;

  height: 40px;
  position: relative;
}

.switchWrapper {
  display: flex;
  z-index: 4;
  align-self: flex-end;
  margin-right: 5px;

  & > div {
    justify-content: flex-end;
  }
  & p {
    opacity: 1;
    font-size: 0.75rem;
  }
}
