.root {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 10px;
}

.text {
  font-size: 16px;
  line-height: 2;
  margin: 0;
  @media (max-width: 600px) {
    font-size: 13px;
  }
}
.line {
  width: 16px;
  height: 2px;
  mix-blend-mode: normal;
  align-self: center;
  margin-left: 4px;
  margin-right: 4px;
  background: #9f8dd0;
  opacity: 0.5;
}
.green {
  color: #bcef90;
}
.red {
  color: #dd5389;
}
