@import "sass/index";

.dialog {
  background: linear-gradient(
      136.08deg,
      #3b1871 0%,
      rgba(11, 4, 21, 0.181709) 38.2%,
      rgba(3, 1, 4, 0.0001) 63.66%,
      #000000 78.79%
    ),
    #0a0d18;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  max-width: 530px;
  width: 100%;

  @media (max-width: 600px) {
    max-width: 340px;
    background: linear-gradient(132.78deg, #24375f 2.42%, #000000 61.15%),
      #0a0d18;
    box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
  }
}
.root {
  margin: 0 30px 30px 30px;
  @media (max-width: 600px) {
    margin: 0 10px 10px 10px;
  }
}
.container {
  display: flex;
  flex-direction: row;
  margin: 0;
  justify-content: space-between;
}
.title {
  color: #9f8dd0;
  opacity: 0.5;
  margin: 0 0 20px 0;
  text-align: left;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 19px;
  @media (max-width: 600px) {
    margin: 0 0 20px 10px;
  }
}
.columnWrapper {
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}
.textWrapper {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  @media (max-width: 600px) {
    margin-top: 0;
  }
}
.dataWrapper {
  background: rgba(255, 255, 255, 0.1);
  width: 225px;
  padding: 20px;
  border-radius: 5px;
  max-width: 225px;

  @media (max-width: 600px) {
    width: 160px;
    padding: 5px;
    &:last-child {
      margin-left: 2px;
    }
  }
}

.dialogActions {
  padding: 30px 30px 20px 30px;
  display: flex;
  @media (max-width: 600px) {
    padding: 20px 20px 10px 20px;
  }
}
.title {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  color: #ffffff;
  @media (max-width: 600px) {
    font-size: 18px;
  }
}
.closeIcon {
  width: 10px;
  height: 10px;
  & .path {
    stroke: use(--template-modal-closeIcon-color);
  }
}
.grow {
  flex: 1 1 auto;
}
