@import "../../variables";

.root {
  position: absolute;
  z-index: 4;
  width: 100%;
  padding: 20px;
  border-radius: 8px;

  overflow: hidden;

  display: flex;
  justify-content: center;
  flex-direction: column;

  @media (max-width: 600px) {
    padding: 10px;
  }

  &__background {
    background: #2f2244;
    &_white {
      background: white;
    }
  }
}
.info {
  position: relative;
  right: 0;
  bottom: 0;
  align-self: flex-end;
  height: 20px;
  width: max-content;
  margin-top: 6px;
  margin-bottom: 5px;

  cursor: pointer;
  font-weight: bold;

  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.8;
}
.info_text {
  color: #9f8dd0;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
}
.info_icon {
  margin-left: 5px;
  width: 18px;
  height: 18px;
  background-image: url(assets/info_icon.svg);
}
.row {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  height: calc((#{$crashTrendsCellSize} + 2px) * (#{$maxRowNumber} + 1));
  position: relative;
  background-size: calc(#{$crashTrendsCellSize} + 2px);
  background-repeat: repeat;
  margin: 0 auto;
  @media (max-width: 600px) {
    height: calc((#{$crashTrendsMCellSize} + 2px) * (#{$maxRowNumber} + 1));
    background-size: calc(#{$crashTrendsMCellSize} + 2px);
  }

  &__background {
    background-image: url(assets/blackCell.png);
    &_white {
      background-image: url(assets/whiteCell.png);
    }
  }
}
