.sliderWrapper {
  display: flex;
  justify-content: flex-start;
  margin: 0 auto;
  width: max-content;
  position: absolute;
  font-weight: 500;

  @media (max-width: 449px) {
    & > :nth-child(-n + 5) {
      display: none;
    }
  }

  @media (max-width: 960px) {
    & > :nth-child(-n + 3) {
      display: none;
    }
  }
}

.historyWrapper {
  overflow: hidden;
  height: 40px;
  position: absolute;
  margin: 0 auto;
  align-self: center;
}
