@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
}
.coinWrapper {
  position: relative;
  height: 100%;
}
.inputWrapper {
  position: relative;
  & :first-child {
    margin: 0;
  }
}
.text {
  text-transform: uppercase;
  font-weight: bold;
  color: use(--secondary-main);

  margin: 0;
  opacity: 0.7;
  @media (max-width: $smallScreen) {
    font-size: 0.685rem;
  }
}
.opportunity {
  @media (max-width: $smallScreen) {
    display: none;
  }
}
.percent {
  height: 100%;
  width: 100%;
  border-radius: 4px;
  cursor: default;

  font-weight: bold;

  display: flex;
  justify-content: center;
  align-items: center;

  background: use(--secondary-main);
  color: use(--primary-main);
}

.coinInput {
  padding-left: 35px;
  margin: 0;
  color: #00000061;
}
